@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: 'Poppins', 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Poppins','Courier New', monospace;
}

.no-city {
  color: #005470;
}

.App {
  text-align: center;
}

.no-city {
  margin-top: 2%;
  font-size: 1.2em;
}

.error,
.warning {
  font-size: 1.5em;
  color: #EC6E4B;
  margin: .5%;
}

header {
  background: url(/hi-lo/static/media/clouds.cd7d61c3.jpg) no-repeat;
  height: 100%;
  width: 100%;
  padding: 1.5em 1.5em;
  border-bottom: 1px solid black;
  font-size: 1.5em;
  color: #073f51;
}

.header-link {
  text-decoration: none;
}



form {
  position: relative;
  display: inline-block;
  margin: 1%;
}

input {
  border: 1px solid transparent;
  background-color: #f1f1f1;
  width: 80%;
  padding: 10px;
  font-size: 1.1em;
}

label {
  font-size: 1.7em;
  color: #005470;
}

button {
  width: 10%;
  border: none;
  border-radius: 5%;
  margin-left: 2%;
  height: 6vh;
  background-color: #005470;
  color: #f1f1f1;
  font-size: .9em;
}

button:hover {
  cursor: pointer;
}

.suggestions {
  position: absolute;
  border: 1px solid #d4d4d4;
  border-bottom: none;
  border-top: none;
  z-index: 99;
  /*position the autocomplete items to be the same width as the container:*/
  top: 100%;
  left: 0;
  right: 0;
}

.suggestions div {
  padding: 10px;
  cursor: pointer;
  background-color: #fff;
  border-bottom: 1px solid #d4d4d4;
}

/*when hovering an item:*/
.suggestions div:hover {
  background-color: #e9e9e9;
}

/*when navigating through the items using the arrow keys:*/

.cities-container {
  padding: 1em;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1em;
}

.card-link {
  text-decoration: none;
}

@media screen and (max-width: 850px) {
  .cities-container {
    grid-template-columns: repeat(3, 1fr);
    grid-gap: .75em;
  }
}

@media screen and (max-width: 550px) {
  .cities-container {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: .5em;
  }
}

@media screen and (max-width: 450px) {
  .cities-container {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: .5em;
  }
}

.city-card {
  text-align: center;
  margin: 0;
  border-radius: 2%;
  border: 1px solid black;
  background-color: #005470;
  color: white;
  padding: 5%;
  height: 43vh;
  width: 25vw;
}

.city-card:hover {
  cursor: pointer;
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  box-shadow: 0px 0px 10px 0px #FFF;
}

.city-img {
  margin-top: 1px;
}

.city-name {
  font-size: 1.8em;
  letter-spacing: 5px;
}

.status {
  font-size: 1.3em;
  font-weight: lighter;
}

.today {
  font-size: 1.7em;
  font-weight: lighter;
}

.hi-lo {
  font-size: 2em;
}

.todays-header {
  font-size: 2em;
  margin-top: .5em;
}

.todays-container {
  color: #005470;
  border-radius: 1%;
  height: 50%;
  margin-left: 15%;
  margin-right: 15%;
  margin-top: 2%;
}

.converted-date {
  font-size: 1.8em;
}

.column-section {
  display: flex;
  justify-content: center;
  font-size: 1em;
}

.left {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 5%;
  margin-bottom: 3%;
  margin-top: -3%;
}

.right {
  display: flex;
  margin-top: 2%;
  padding-right: 10%;
}

.curr-temp {
  font-size: 5em;
  font-weight: bolder;
}

.high,
.low {
  font-size: 1.7em;
  margin-top: -10%;
  font-weight: bolder;
}

li {
  list-style-type: none;
}

ul {
  margin-top: -30%;
  font-size: .9em;
}

.hourly-forecast {
  color: whitesmoke;
  background-color: #4B93A6;
  border-radius: 5%;
  height: 70%;
  width: 90%;
  margin: 2%;
}

.loading {
  font-size: 2em;
  color: #005470;
  margin-top: 5%;
}


.fiveday-title {
  color: #005470;
  font-size: 2em;
  text-align: left;
  margin-left: 1%;
}

.all-days {
  display: flex;
  justify-content: space-evenly;
  width: 100vw;
  font-family: 'Roboto', sans-serif;
  font-size: 2.5em;
  font-weight: bold;
  color: white;
  background: rgb(6,119,144);
  padding: 30px;
  background-color: rgba(6,119,144, 0.8);
}

.day-two-hi,
.day-three-hi,
.day-four-hi,
.day-five-hi {
  font-size: 25px;
  margin-top: -15%;
}

.day-two-lo,
.day-three-lo,
.day-four-lo,
.day-five-lo {
  font-size: 25px;
}

.day-two-date,
.day-three-date,
.day-four-date,
.day-five-date {
  margin: 0;
  padding: 0;
}

.day-two-icon,
.day-three-icon,
.day-four-icon,
.day-five-icon {
  height: 16vh;
  width: 16vh;
  margin-top: -15%;
}

.day-two,
.day-three,
.day-four,
.day-five {
  margin: 0;
}

