

form {
  position: relative;
  display: inline-block;
  margin: 1%;
}

input {
  border: 1px solid transparent;
  background-color: #f1f1f1;
  width: 80%;
  padding: 10px;
  font-size: 1.1em;
}

label {
  font-size: 1.7em;
  color: #005470;
}

button {
  width: 10%;
  border: none;
  border-radius: 5%;
  margin-left: 2%;
  height: 6vh;
  background-color: #005470;
  color: #f1f1f1;
  font-size: .9em;
}

button:hover {
  cursor: pointer;
}

.suggestions {
  position: absolute;
  border: 1px solid #d4d4d4;
  border-bottom: none;
  border-top: none;
  z-index: 99;
  /*position the autocomplete items to be the same width as the container:*/
  top: 100%;
  left: 0;
  right: 0;
}

.suggestions div {
  padding: 10px;
  cursor: pointer;
  background-color: #fff;
  border-bottom: 1px solid #d4d4d4;
}

/*when hovering an item:*/
.suggestions div:hover {
  background-color: #e9e9e9;
}

